#preview-container {
  padding-top: 15px;


  animation: fadeIn 1.0s ease;
  -webkit-animation: fadeIn 1.0s ease-in-out;
  -moz-animation: fadeIn 1.0s ease-in-out;
  -o-animation: fadeIn 1.0s ease-in-out;
  -ms-animation: fadeIn 1.0s ease-in-out;
}

#preview-image {
  position: relative;
  width: 90%;
  max-height: 800px;
  transition: opacity 2s ease-out;
  object-fit: scale-down;
  margin: 20px;

  box-shadow: 20px 15px;
}

#placeholder-image {
  position: relative;
  width: 90%;
  max-height: 300px;
  transition: opacity 2s ease-out;
  object-fit: scale-down;
  margin: 20px;
}

#image-onLoad {
  width: 100%;
  transition: opacity 2s ease-out;
  object-fit: scale-down;
  border-width: 40px;
  border-style: solid;
  border-color: white;
  animation: fadeIn 2.0s ease;
  -webkit-animation: fadeIn 2.0s ease;
  -moz-animation: fadeIn 2.0s ease;
  -o-animation: fadeIn 2.0s ease;
  -ms-animation: fadeIn 2.0s ease;
}

#preview-header {
  z-index: 99;
  font-size: 30px;
  /* 
  position: absolute;
  background-image:inherit;
  background: inherit;
  background-clip:text;
  color:transparent;
  filter: invert(0) grayscale(1) constrast(9); */
}

/* ANIMATIONS */

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-o-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-moz-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-ms-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes kenburns {
  0% {
    opacity: 0;
  }
  5% {
    opacity: 1;
  }
  95% {
    transform: scale3d(1.2, 1.2, 1.2) translate3d(-30px, -20px, 0px);
    animation-timing-function: ease-in;
    opacity: 1;
  }
  100% {
    transform: scale3d(1.4, 1.4, 1.4) translate3d(-200px, -10px, 0px);
    opacity: 0;
  }
}